import styled from 'styled-components';
import fofbb from './../fonts/fofbb_ital.otf';
export const HeroContainer = styled.div`

    
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 800px;
    position: relative;
    z-index: 1;
    @font-face {
        font-family: fofbb;
        src: url(${fofbb});
    }
`

export const HeroBg = styled.div`
    position: absolute;
    top: 0vh;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 120vh;
    overflow: hidden;
    background-color: #000;

    @media screen and (max-width: 768px){
        height: 225vw;
    }
`

export const VideoBg = styled.video`

    width: 100%;
    height: 120vh;
    -o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
    opacity: 0.3;

`


export const HeroRow = styled.div`
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    justify-content: center;
    grid-template-areas: 'col1 col2';

    @media screen and (max-width: 768px){
        grid-template-areas: 'col1 col1' 'col2 col2';
    }
`;

export const Column1 = styled.div`
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col1;
`;

export const Column2 = styled.div`
    padding: 0 15px;
    grid-area: col2;
`;

export const HeroContent = styled.div`
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width:468px){
        padding-top: 20vh;
    }
`;

export const HeroH1 = styled.h1`
    color: #fff;
    font-size: 48px;
    text-align: center;
    font-family: fofbb;
    letter-spacing: .3vh;

    @media screen and (max-width: 768px){
        font-size: 40px;
    }

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`;

export const HeroImgWrapper = styled.div`
    max-width: 555px;
    height: 100%;
`;

export const HeroImg = styled.img`
    width: 100%;
    border-radius: 20px;
`;

export const Column2Wrapper = styled.div`
    max-width: 600px;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10%;
`;

export const MintBtn = styled.button`
    height: 20%;
    width: 40%;
    background: none;
    border: 1px solid #F39DB6;
    white-space: nowrap;
    color: #fff;
    font-size: 16px;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    font-family: AtWritter;
    text-decoration: underline;
    font-size: 1.5vh;

    &:hover{
        background-color: #F39DB6;
        transition: all 0.2s ease-in-out;
    }
`;

export const ButtonWrap = styled.div`
    display: flex;
    justify-content: flex-start;
    width: auto;
    height: 20%;
    margin-top: 5%;
`
