import React from 'react'
import logo from '../../Image/retroapeclub.png';
import {FaBars} from 'react-icons/fa' 



import {
    Nav, 
    NavbarContainer, 
    NavLogo,
    ImgLogo, 
    MobileIcon, 
   
    NavBtn,
    
    NavBtnContainer
    

} from './NavbarElements';



const Navbar = ({ toggle,initializeWallet, isInitialized, account }) => {

    const patt = /^[a-zA-Z0-9]{10}/;
    const sanitizedAddress = account.match(patt);
    return (
        <>
            <Nav>
                <NavbarContainer>
                    <NavLogo to="/">
                        <ImgLogo src={logo}></ImgLogo>
                    </NavLogo>

                    <MobileIcon onClick={toggle}>
                        <FaBars />
                    </MobileIcon>

                    <NavBtnContainer>
                        <NavBtn onClick={initializeWallet}>{(isInitialized ? sanitizedAddress : account)}</NavBtn>
                    </NavBtnContainer>

                </NavbarContainer>
            </Nav>
        </>
    )
}

export default Navbar;
