import styled from 'styled-components';
import { Link as LinkR} from 'react-router-dom';
import { Link as LinkS} from 'react-scroll';
import fofbb from '../../fonts/fofbb_ital.otf';



export const Nav = styled.nav`
    background: #000;
    height: 80px;
    /* margin-top: --80px; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;

    @font-face {
    font-family: 'fofbb';
    src: url(${fofbb});
}
    
    @media screen and (max-width: 960px){
        transition: 0.8s all ease;
    }
`;

export const NavbarContainer = styled.div`
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1100px;

`;

export const NavLogo = styled(LinkR)`
    color: #fff;
    justify-self: flex-start;
    cursor: pointer;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-left: 24px;
    font-weight: bold;
    text-decoration: none;
`;

export const ImgLogo = styled.img`
    display: flex;
    height: 80%;
    width: auto;
    margin-right: 1vw;

    @media screen and (max-width: 480px){
        margin-right: 4vw !important;
    }
`;


export const MobileIcon = styled.div`
    display: none;

    @media screen and (max-width: 768px){
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        color: #fff;
    }
`


export const NavMenu = styled.ul`

    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    margin-right: -22px;


    @media screen and (max-width: 768px){
        display: none;
    }
`;

export const NavItem = styled.li`

    height: 80px;

    

`;

export const NavLinks = styled(LinkS)`

    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    

    &.active{
        border-bottom: 3px solid #01bf71;
    }
`;


export const NavBtnContainer = styled.nav`
    display: flex;
    align-items: center;
    @media screen and (max-width: 768px){
        display: none;
    }
`;

export const NavBtn = styled.button`
    border-radius: 2px;
    border:2px solid #0b6591;
    background: #1c1131;
    white-space: nowrap;
    padding: 10px 22px;
    color: white;
    font-size: 16px;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;  

    &:hover{
        
        transition: all 0.2s ease-in-out;
        background: #372659;
        border:2px solid #2facea;
        color: white;
        
    }
`

