import React from 'react'
import Video from './../videos/video2.mp4'
import ape from '../Image/apeclub.png';
import { HeroContainer, HeroBg,  VideoBg,HeroContent, HeroH1, HeroRow, HeroImgWrapper, HeroImg, Column1, Column2, Column2Wrapper, ButtonWrap} from './HeroElements'


const HeroSection = () => {
    return (
        <HeroContainer>
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video2/mp4' />
            </HeroBg>

            <HeroContent>
                <HeroRow>
                        <Column1>
                            <HeroImgWrapper>
                                <HeroImg src={ape}></HeroImg>
                            </HeroImgWrapper>
                        </Column1>

                        <Column2>
                            <Column2Wrapper>
                                <HeroH1>RETRO APE CLUB!</HeroH1>
                               
                               
                            </Column2Wrapper>
                        </Column2>              
                </HeroRow>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
